@import "../../../styles/vars";
@import "../../../styles/mixins";

.uiUxDesignExamples {
  padding-top: 300px;
  background: $color-black;

  @include xlScreenMedia {
    padding-top: 270px;
  }

  @include lgScreenMedia {
    padding-top: 220px;
  }
  @include mdScreenMedia {
    padding-top: 150px;
  }

  @include smScreenMedia {
    padding-top: 60px;
  }

  &__container {
    display: block;
    @include setDistance(padding-top, 140);
    @include setDistance(padding-bottom, 70);
  }

  &__title {
    text-align: center;
    @include setDistance(margin-bottom, 60);
  }

  &__marque {

    :global(.marquee) {
      width: 200%;

      @include mdScreenMedia {
        width: 1200%;
      }
    }
  }

  &__imgList {
    margin: 0 -10px;

    &_desk {
      @include mdScreenMedia {
        display: none;
      }
    }

    &_mob {
      @media (min-width: $md-screen) {
        display: none;
      }
    }
  }

  &__slide {
    padding: 0 10px !important;
    display: flex !important;
    flex-direction: column;
    gap: 40px;
  }
  &__img {
    width: 100%;
  }
}
